<template>
  <v-container>
    <v-row>
      <v-col cols="7">
        <d3-network
         :net-nodes="persons"
         :net-links="links"
         :options="options" />
      </v-col>
      <v-col cols="5">
        <person-create v-on:add-person="addPerson"></person-create>
        <v-divider></v-divider>
        <v-row>
          <v-col cols="12">
            <person-list v-bind:persons="persons"></person-list>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import D3Network from 'vue-d3-network'
import PersonList from './PersonList'
import PersonCreate from './PersonCreate'

const mbtiIndex = {
  'ENTJ': 0, 'ENTP': 1, 'INTJ': 2, 'INTP': 3, 
  'ESTJ': 4, 'ESFJ': 5, 'ISTJ': 6, 'ISFJ': 7,
  'ENFJ': 8, 'ENFP': 9, 'INFJ': 10, 'INFP': 11,
  'ESTP': 12, 'ESFP': 13, 'ISTP': 14, 'ISFP': 15,
}

const mbtiScores = [
  [1, 2, 3, 3, 2, 1, 2, 1, 2, 3, 3, 5, 4, 4, 3, 5],
  [2, 4, 3, 3, 4, 4, 5, 5, 4, 2, 4, 4, 2, 1, 2, 1],
  [3, 3, 3, 2, 2, 1, 3, 1, 3, 3, 3, 4, 5, 5, 3, 4],
  [3, 3, 2, 3, 4, 5, 3, 5, 5, 3, 4, 2, 2, 1, 2, 1],
  
  [2, 4, 2, 4, 3, 2, 3, 3, 1, 3, 1, 5, 1, 3, 3, 5],
  [1, 4, 1, 5, 2, 3, 3, 3, 3, 4, 2, 3, 3, 2, 5, 3],
  [2, 5, 3, 3, 3, 3, 3, 2, 1, 5, 1, 4, 3, 3, 2, 4],
  [1, 5, 1, 5, 3, 3, 2, 2, 2, 5, 2, 3, 3, 3, 4, 1],

  [2, 3, 3, 5, 1, 3, 1, 2, 4, 2, 3, 3, 4, 4, 5, 3],
  [3, 2, 3, 3, 4, 4, 5, 5, 2, 3, 3, 2, 1, 1, 1, 3],
  [3, 3, 3, 4, 1, 2, 1, 2, 3, 3, 3, 1, 5, 5, 5, 3],
  [5, 3, 4, 2, 5, 3, 4, 3, 3, 2, 1, 3, 1, 2, 2, 2],

  [4, 2, 5, 2, 1, 3, 3, 3, 4, 1, 5, 1, 3, 2, 3, 3],
  [4, 1, 5, 1, 3, 2, 3, 3, 4, 1, 5, 2, 2, 2, 3, 3],
  [3, 2, 3, 2, 3, 5, 2, 4, 5, 1, 5, 1, 3, 3, 2, 2],
  [5, 1, 4, 1, 5, 3, 4, 1, 3, 3, 3, 2, 3, 3, 2, 3],
]

const matchColors = [
  'lighgray', 'red', 'orange', 'lightgreen', 'green', 'blue'
]

const compatibilityLabels = [
  '', '최악', '다소 불편', '불편하지는 않음', '친해지기 쉬움', '마음 잘 맞음'
]

const showLinkLabel = true

export default {
  name: 'home',
  components: {
    D3Network,
    PersonList,
    PersonCreate,
  },
  data() {
    return {
      nodeSize: 30,
      canvas: false,
      persons: [
        { id: 1, name: 'John', mbtiType: 'INFP' },
        { id: 2, name: 'Alice', mbtiType: 'ESTJ' },
        { id: 3, name: 'Brad', mbtiType: 'ISTJ' },
      ],
    }
  },
  computed: {
    options() {
      return {
        force: 9000,
        size: {
          w: 600, h: 600
        },
        nodeSize: this.nodeSize,
        nodeLabels: true,
        linkLabels: true,
        linkWidth: 4,
        canvas: this.canvas
      }
    },
    links: function() {
      var links = []
      let size = this.persons.length
      for (var i = 0; i < size - 1; i++) {
        for (var j = i + 1; j < size; j++) {
          let score = getMatchScore(this.persons[i], this.persons[j])
          links.push({
            sid: this.persons[i].id, 
            tid: this.persons[j].id,
            _color: getColor(score),
            name: getLinkLabel(score),
            })
        }
      }
      return links
    },
  },
  methods: {
    addPerson(person) {
      this.persons.push(person);
    },
    sendForm() {
      const mbtiType = this.mbtiType
      const name = this.name
      this.$emit('addPerson', {
        id: 11,
        mbtiType: mbtiType,
        name: name,
      })
    }
  }
}

function getMatchScore(node1, node2) {
  let idx1 = mbtiIndex[node1.mbtiType]
  let idx2 = mbtiIndex[node2.mbtiType]
  return mbtiScores[idx1][idx2]
}

function getColor(score) {
  return matchColors[score]
}

function getLinkLabel(score) {
  if (!showLinkLabel) {
    return ''
  }
  return compatibilityLabels[score]
}
</script>
<style src="vue-d3-network/dist/vue-d3-network.css"></style>
<style>
.node-label {
  font-size: 20px !important;
  font-weight: 700;
}
</style>